import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup } from '@angular/forms';
import Inputmask from 'inputmask';
import { PsAccountsListComponent } from 'src/app/commonBussinessSRC/psComponents/ps-accounts-list/ps-accounts-list.component';
import { IOptionsPsComplexExchnageExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-exchange/ps-complex-exchange.component.interfaces';
import { IOptionsPsComplexSelectBranchComponentExposed } from 'src/app/commonBussinessSRC/psComponents/ps-complex-components/ps-complex-select-branch/ps-complex-select-branch.component.interface';
import { IOptionsPsInputVarcharExposed } from 'src/app/commonBussinessSRC/psComponents/ps-keyin-input/ps-input-varchar/ps-input-varchar.component.interfaces';
import { IOptionsPsLookupOwnAccountsExposed } from 'src/app/commonBussinessSRC/psComponents/ps-lookup/ps-lookup-own-accounts/ps-lookup-own-accounts.component.interfaces';
import { IOptionsPsMapAtmBranchesExposed } from 'src/app/commonBussinessSRC/psComponents/ps-map-atm-branches/ps-map-atm-branches.component.interfaces';
import { IOptionsPsDropdownAccountTypesExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-account-types/ps-dropdown-account-types.component.interface';
import { IOptionsPsDropdownCurrenciesExposed } from 'src/app/commonBussinessSRC/psComponents/ps-select-dropdown/ps-dropdown-currencies/ps-dropdown-currencies.component.interfaces';
import { IOcBranchesRequest, IValidateCurrencyCombination } from 'src/app/commonBussinessSRC/psServices/models/ps-common-bussiness-interfaces';
import { PsCommonBusinessSettings } from 'src/app/commonBussinessSRC/psServices/models/ps-commonBusiness.settings';
import { OmniCommonService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-common.service';
import { OmniPullService } from 'src/app/commonBussinessSRC/psServices/omni-common/omni-pull.service';
import { EventEmitterService } from 'src/app/commonSRC/psServices/event-emitter/event-emitter.service';
import { LoggerService } from 'src/app/commonSRC/psServices/logger/logger.service';
import { ConstantCommon } from 'src/app/commonSRC/psServices/models/common-constant';
import { CommonUtils } from 'src/app/commonSRC/psServices/models/common-utils';
import { IOptionsPsContainerPanel, IOptionsPsKeyinInputExposed, IOptionsPsSelectToggle, IOptionsTemplateStepper, IPsMaskOptions } from 'src/app/commonSRC/psServices/models/ps-common-interface';
import { PsCommonSettings } from 'src/app/commonSRC/psServices/models/ps-common.settings';
import { PsNavigatorService } from 'src/app/commonSRC/psServices/navigator/ps-navigator.service';
import { PsCommonService } from 'src/app/commonSRC/psServices/ps-common/ps-common.service';
import { CommonBussinessConstant } from '../../commonBussinessSRC/psServices/models/ps-common-bussiness-constant';
import { OmniBasePage } from '../omni-base/omni-base.page';

@Component({
  selector: 'account-opening',
  templateUrl: './account-opening.page.html',
  styleUrls: ['./account-opening.page.scss'],
})
export class AccountOpeningPage extends OmniBasePage implements OnInit {
  formGroup: FormGroup = new FormGroup({});
  defaultVO: any = {};

  cifInfo: any;
  branchVal: any;
  @ViewChild('fromAccountRef') fromAccountRef;
  @ViewChild('exchangeRef') exchangeRef;
  @ViewChild('currenciesRef') currenciesRef;
  mapAtmBranchesOptions: IOptionsPsMapAtmBranchesExposed = {
    group: this.formGroup,
    fcName: 'mapAtmBranches',
    labelKey: 'map_atm_branch_key',
    mapOptions: {
      labelKey: 'map'
    },
    showSegments: false,
    mapTypesInclude: '\'B\'',
    branchIds: '1,3,5',
    parameterToCheck: CommonBussinessConstant.RequestBranch,
    requestVO: this.defaultVO
  };
  mask: IPsMaskOptions = {
    groupSeparator: PsCommonSettings.THOUSANDS_SEPARATOR,
    alias: 'currency',
    autoGroup: true,
    placeholder: '0',
    greedy: false,
    digits: 2,
    radixPoint: PsCommonSettings.DECIMAL_SEPARATOR
  };
  stepperOptions: IOptionsTemplateStepper = {
    stepperName: 'acc_open_stepper',
    isHorizontalStepper: true,
    numberOfSteps: 2,
    namesofSteps: ['acc_open_step1', 'acc_open_step2'],
    group: this.formGroup,
    submitOptions: {
      extraParams: {},
      submitServiceUrl: PsCommonBusinessSettings.serviceUrl.accountOpeningEndPoint,
      group: this.formGroup
    },
    requestObject: this.defaultVO
  };


  accountTypeOptions: IOptionsPsDropdownAccountTypesExposed = {
    allowedAccountType: ConstantCommon.AllowedGeneralAccountTypes,
    accountCategory: ConstantCommon.ACC_TYPE_D,
    group: this.formGroup,
    fcName: 'accountTypes'
  };

  openingBalanceToggleOptions: IOptionsPsSelectToggle = {
    labelKey: 'add_opening_balance_key',
    placeHolder: 'add_opening_balance_key',
    group: this.formGroup,
    fcName: 'addOpeningBalance',
    psClass: 'toggleColor'
  };

  minOpeningBalOptions: IOptionsPsKeyinInputExposed = {
    placeHolder: 'minimum_opening_balance_key',
    labelKey: 'minimum_opening_balance_key',
    group: this.formGroup,
    fcName: 'minOpenBalance'
  };

  public fromAccOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'from_account_key',
    placeHolder: 'select_from_account_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'fromAccount',
    accountType: 'G',
    fromTo: 'from',
    requestObject: this.defaultVO
  };

  public complexExchangeOptions: IOptionsPsComplexExchnageExposed = {
    editableMode: false,
    showToCurrencyOptions: true,
    fromAmountOptions: {
      currency: '',
      currenciesOptions: {
        placeHolder: 'currency_key',
        labelKey: 'exchange_currency_key',
        fcName: 'fromCurrency',
        group: this.formGroup,
        revertToOldValue: false
      },
      amountOptions: {
        labelKey: 'amount_key',
        placeHolder: 'enter_amount_key',
        fcName: 'transactionAmount',
        group: this.formGroup,
        decimalPoints: 2
      }
    },
    toAmountOptions: {
      currency: '',
      currenciesOptions: {
        placeHolder: 'currency_key',
        labelKey: 'currency_key',
        fcName: 'toCurrency',
        group: this.formGroup
      },
      amountOptions: {
        labelKey: 'exchange_amount_key',
        placeHolder: 'enter_amount_key',
        fcName: 'toAmount',
        group: this.formGroup,
        decimalPoints: 2
      }
    },
    requestObject: this.defaultVO,
    group: this.formGroup
  };

  accountNameOptions: IOptionsPsInputVarcharExposed = {
    labelKey: 'name_key',
    placeHolder: 'name_key',
    group: this.formGroup,
    fcName: 'accountName'
  };
  currencyOptions: IOptionsPsDropdownCurrenciesExposed = {
    labelKey: 'currency_key',
    placeHolder: 'select_currency_key',
    group: this.formGroup,
    fcName: 'currency',
    revertToOldValue: false
  };
  panelOptions1Step1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'account_details_key',
    iconName: 'document',
    expanded: true
  };
  panelOptions2Step1: IOptionsPsContainerPanel = {
    isExpandable: true,
    labelKey: 'select_branch_key',
    iconName: 'document',
    expanded: true
  };

  branchLabelOptions: IOptionsPsKeyinInputExposed = {
    placeHolder: 'user_branch_key',
    labelKey: 'branch_key',
    group: this.formGroup,
    fcName: 'branch_type'
  };

  selectBranchOptions: IOptionsPsComplexSelectBranchComponentExposed = {
    fcName: 'mapAtmBranchesDropdown',
    group: this.formGroup,
    branchesOptions: {
      placeHolder: 'acc_opening_branch_key',
      labelKey: 'label_acc_opening_branch_key',
      group: this.formGroup,
      fcName: 'default_branch'

    }
  };
  toggleProfitOptions: IOptionsPsSelectToggle = {
    labelKey: 'post_profit_to_same_account_key',
    placeHolder: 'post_profit_to_same_account_key',
    group: this.formGroup,
    fcName: 'renewWithSame',
    psClass: 'toggleColor',
  }

  profitAccOptions: IOptionsPsLookupOwnAccountsExposed = {
    labelKey: 'post_profit_to_key',
    placeHolder: 'select_post_profit_to_key',
    component: PsAccountsListComponent,
    group: this.formGroup,
    accountAllowedCurrencies: [],
    accountAllowedTypes: [],
    glTypes: 'G',
    fcName: 'profitAccount',
    accountType: 'G',
    fromTo: 'to',
    requestObject: this.defaultVO
  };
  selectedBranchObj = {};
  enableCifBranch;
  addOpeningBal = false;
  showProfitToggle = false;
  showProfitAccount = false;
  constructor(public commonProv: PsCommonService,
    public loggerP: LoggerService,
    public eventEmitterService: EventEmitterService,
    private omniPull: OmniPullService,
    private commonService: OmniCommonService,
    public navService?: PsNavigatorService) {
    super();
    this.fetchParameter();
  }


  ngOnInit() {
    super.init();
    this.baseFormGroup = this.formGroup;
    this.cifInfo = this.commonProv.session.getValueOf(ConstantCommon.USERINFO);
    const defVal = this.commonProv.getElementValidations(this.openingBalanceToggleOptions.fcName)?.DEFAULT_VALUE;
    if (defVal) {
      try {
        const parsed = JSON.parse(defVal);
        if (parsed.value === true) {
          this.addOpeningBal = true;
          this.commonProv.applyDynScreenDisplay(
            ConstantCommon.ACTION_TYPE_MANDATORY,
            [this.complexExchangeOptions.fromAmountOptions.amountOptions.fcName],
            1
          );
          this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.minOpeningBalOptions.fcName], 1);
        }
      } catch (e) {
        console.warn("Invalid DEFAULT_VALUE:", defVal, e);
      }
    }
  }

  ionViewDidEnter() {
    super.ionViewDidEnter();
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.complexExchangeOptions.toAmountOptions.amountOptions.fcName], 1);
    this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.openingBalanceToggleOptions.fcName], 0);
  }

  onSelectBranch($event) {
    if ($event.itemValue !== undefined) {
      this.formGroup.controls[this.selectBranchOptions.fcName].setValue($event);
      this.commonProv.setValInsideNestedObj(this.selectBranchOptions.fcName, $event.selectedObj, this.defaultVO);
    }
  }

  loadBranch() {
    try {
      const paramData: IOcBranchesRequest = {
        branchesIdList: this.cifInfo.cifBranch ? this.cifInfo.cifBranch : ''
      };
      this.omniPull.returnBranchesList(paramData).then((result) => {
        if (result && result.gridModel != null && result.gridModel.length > 0) {
          this.formGroup.controls[this.branchLabelOptions.fcName].setValue(this.cifInfo.cifBranch ? result.gridModel[0].longDesc : '');
        } else { this.loggerP.warn(result, 'empty response'); }
      }).catch((error) => { });
    } catch (error) { }
  }
  get getBranch(): number {
    return this.cifInfo.customerInfoCO ? this.cifInfo.customerInfoCO.branchCode : 1;
  }
  async fetchParameter() {
    await this.omniPull.getParamValOf(CommonBussinessConstant.RequestBranch, CommonBussinessConstant.AllowedBranches).then((result) => {
      if (result !== -1 && result.RequestBranch == null || result.RequestBranch === ConstantCommon.cifBranch) {
        this.enableCifBranch = true;
        this.commonService.branchSelectionAndMapControl(0, [{ fcName: this.stepperOptions.namesofSteps[1] }, { fcName: this.mapAtmBranchesOptions.fcName },
        { fcName: 'account_opening_panel_2' }], true);
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.branchLabelOptions.fcName], 1);
        //set user default branch code and show user branch in label 
        this.commonProv.setValInsideNestedObj('branchCode', this.cifInfo.customerInfoCO?.branchCode || "", this.formGroup.controls['formData'].value);
        const aBranch = result.allowedBranches ? result.allowedBranches.toString().split(',') : result.AllAllowedBranches.split(',');
        if (result.RequestBranch === ConstantCommon.cifBranch && aBranch.toString().indexOf(this.cifInfo.cifBranch.toString()) === -1) {
          CommonUtils.presentFatalAlert('Cannot_open_General_Account_in_your_CIF_Branch._Please_contact_our_call_center_key');
          this.navService.navigateBackward();
        } else {
          setTimeout(() => {
            this.commonProv.setValInsideNestedObj(this.branchLabelOptions.fcName, this.cifInfo.cifBranch ? this.cifInfo.customerInfoCO.branchBriefDesc : '', this.defaultVO);
          }, 300);
        }
      } else {
        this.enableCifBranch = false;
        this.commonService.branchSelectionAndMapControl(1, [{ fcName: this.stepperOptions.namesofSteps[1] },
        { fcName: this.mapAtmBranchesOptions.fcName, mandatory: 1 },
        { fcName: 'account_opening_panel_2' }]);
        //hide banch label
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_VISIBLE, [this.branchLabelOptions.fcName], 0);
      }
    }).catch((error) => {
    });
  }

  onAddOpeningBalanceChange(event) {
    if (event && event !== undefined) {
      if (event.newValue === true || event.newValue === "true") {
        this.addOpeningBal = true;
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.complexExchangeOptions.fromAmountOptions.amountOptions.fcName], 1);
      } else {
        this.addOpeningBal = false;
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_MANDATORY, [this.complexExchangeOptions.fromAmountOptions.amountOptions.fcName], 0);
      }
    }
  }

  onFromAccountCombinationValidation(fromAccount) {
    if (fromAccount && fromAccount[ConstantCommon.VALIDATE_COMBINATION]) {
      if (this.defaultVO[this.currencyOptions.fcName] instanceof Object) {
        this.validateCurrencyCombination();
      }
    }
  }

  public onFromAccountChange(fromAccount) {
    this.onFromAccountCombinationValidation(fromAccount);
    this.complexExchangeOptions.fromAmountOptions.currency = fromAccount.currencyBriefNameEnglish;
    this.complexExchangeOptions.fromAmountOptions.currencyCode = fromAccount.currency;
    setTimeout(() => {
      this.stepperOptions.requestObject[this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName] = fromAccount.currency;
    }, 1);
  }

  onCurrencyChanged(currency) {
    this.onCurrencyCombinationValidation();
    this.complexExchangeOptions.toAmountOptions.currency = currency.selectedObj.briefDescription;
    this.complexExchangeOptions.toAmountOptions.currencyCode = currency.itemValue;
    this.stepperOptions.requestObject[this.complexExchangeOptions.toAmountOptions.currenciesOptions.fcName] = currency.itemValue;
  }

  onAccountTypeChanged(event) {
    if (event) {
      if (event.minOpenBal) {
        this.commonProv.setValInsideNestedObj(this.minOpeningBalOptions.fcName, this.cifInfo.companySettingsCO?.briefDesc + ' ' + Inputmask.format(event.minOpenBal, this.mask).toString(), this.defaultVO);
      }
      const profitToggleCust = this.commonProv.getElementValidations(this.toggleProfitOptions.fcName);
      const profitAccountCust = this.commonProv.getElementValidations(this.profitAccOptions.fcName);
      if (event.postProfitTo == CommonBussinessConstant.POST_PROFIT_TO_DEPOSITE_ACCOUNT || event.postProfitTo == CommonBussinessConstant.POST_PROFIT_TO_PROFIT_ACCOUNT) {
        this.showProfitToggle = (PsCommonSettings.custMode || profitToggleCust.IS_VISIBLE) ? true : false;
        this.showProfitAccount = (PsCommonSettings.custMode || profitAccountCust.IS_VISIBLE) && event.postProfitTo == CommonBussinessConstant.POST_PROFIT_TO_PROFIT_ACCOUNT
        this.commonProv.applyDynScreenDisplay(ConstantCommon.ACTION_TYPE_READONLY, [this.toggleProfitOptions.fcName], 1);
        this.commonProv.setValInsideNestedObj(this.toggleProfitOptions.fcName, event.postProfitTo == CommonBussinessConstant.POST_PROFIT_TO_DEPOSITE_ACCOUNT, this.defaultVO);
      } else {
        this.showProfitToggle = this.showProfitAccount = false;
      }
      //Added by Marina AZDB-1208; to get allowed currenies for this account type only
      if (event.genLedgerId) {
        this.currencyOptions.accountTypesGLIds = event.genLedgerId;
        this.currenciesRef.loadCurrencies();
      }
    }
  }

  onCurrencyCombinationValidation() {
    if (this.defaultVO[this.fromAccOptions.fcName] instanceof Object) { //added by MichelHabib, tp#1583589 in order to check if the fromAccount is an object to be able to make the validation(in case of checker user on initialization of the screen fromAccount has a string value)
      this.validateCurrencyCombination();
    }
  }


  validateCurrencyCombination() {
    let validationRequest: IValidateCurrencyCombination = {
      fromCurrency: +this.defaultVO[this.fromAccOptions.fcName].currency,
      listOfAffectedFields: [this.complexExchangeOptions.fromAmountOptions.currenciesOptions.fcName, this.currencyOptions.fcName],
      screenVO: this.defaultVO,
      listOfAccounts: [this.fromAccountRef, this.exchangeRef],
      toCurrency: +this.defaultVO[this.currencyOptions.fcName].itemValue,
    };
    this.omniPull.validateCurrencyCombination(validationRequest);
  }
}

